.header {
    position: fixed;

    display: flex;
    flex-direction: row;
    align-items: space-between;
    justify-content: top;

    text-decoration: none;

    height: 7.5em;
    width: 100vw;

    background: #F6F8FB;
    background: linear-gradient(to top, rgba(255,0,0,0), #F6F8FB);
}

.header .right {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;

    margin-top: 2.5em;
    margin-left: 2.5em;
}


@media (max-width: 900px) {
    .header {
        align-items: center;
        justify-content: center;
    }

    .header .right {
        align-items: center;
        justify-content: center;
        margin-left: 0em;
        margin-right: 0em;
    }
}
.nav-button {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;

    height: 3.125em;
    width: 9.375em;

    font-family: DM Sans;
    font-size: 1em;
    font-style: normal;
    letter-spacing: 0.1em;
    text-align: right;
    color: #000000;

    background: transparent;

    margin-right: 25px;
}

.nav-button .text {
    line-height: 1.75;
    border-bottom: 2px solid transparent;
}

.nav-button .text:hover {
    line-height: 1.75;
    border-bottom: 2px solid black;
}


.rectangle-polaroid {
    min-width: 15.5em;
    height: 100%;

    background: white;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    transition: transform .3s;

    margin-left: 1em;
    margin-right: 1em;
    margin-bottom: 1em;

    border: 2px solid;
    border-radius: 12px;
    border-color: transparent;
}

.rectangle-polaroid .image {
    width: 100%;
    border-radius: 10px 10px 0px 0px;
    overflow: hidden;
}

.rectangle-polaroid .text {
    border-radius: 10px;

    display: flex;
    flex-direction: column;
    justify-content: space-between;
    overflow: hidden;
}

.rectangle-polaroid .text .title {
    font-family: DM Sans;
    font-style: normal;
    font-weight: bold;
    font-size: 1em;
    line-height: 1.75em;
    color: #000000;

    margin-top: 2em;
    margin-bottom: 0em;
    margin-left: 2em;
    margin-right: 2em;
}

.rectangle-polaroid .text .subtitle {
    font-family: DM Sans;
    font-style: normal;
    font-weight: bold;
    font-size: 1em;
    line-height: 1.75em;
    color: #4285F4;

    margin-top: 0em;
    margin-bottom: 0em;
    margin-left: 2em;
    margin-right: 2em;
}

.rectangle-polaroid .text .description {
    font-family: DM Sans;
    font-style: normal;
    font-weight: normal;
    font-size: 1em;
    line-height: 1.75em;
    color: #000000;

    margin-top: 1em;
    margin-bottom: 2em;
    margin-left: 2em;
    margin-right: 2em;
}

.rectangle-polaroid:hover {
    border: 2px solid;
    border-radius: 12px;
    border-color: #4285F4;

    background: rgba(66, 133, 244, 0.5);
    box-shadow: 6px 6px 6px rgba(0, 0, 0, 0.025);
}
.tag {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;

    height: -webkit-fit-content;

    height: -moz-fit-content;

    height: fit-content;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    padding-top: 0.5em;
    padding-bottom: 0.5em;
    padding-left: 1.5em;
    padding-right: 1.5em;
