    background: transparent;

    border: 2px solid;
    border-radius: 15px;
    border-color: #4285F4;

    font-family: DM Sans;
    font-size: 0.85em;

    margin-right: 10px;
    margin-bottom: 10px;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.tag .text {
    margin-top: 0px;
    color: #4285F4;
}

.tag:hover {
    color: #00A1F1;
}


.icon-button {
    height: 40px;
    width: 40px;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    background: transparent;
    color: #4285F4;

    font-size: 16px;

    border: 2px solid;
    border-radius: 10px;
    border-color: #4285F4;
    background: rgba(66, 133, 244, 0.35);

    margin-left: 0.25em;
    margin-right: 0.25em;
}

.icon-button:hover {
    color: white;

    border: 2px solid;
    border-radius: 10px;
    border-color: #4285F4;

    background: rgba(66, 133, 244, 0.65);
